import { Component, OnInit, ViewChild, AfterViewInit } from '@angular/core';
import model_docs from "../../assets/documentation.json";
import { TreeComponent, TreeModel, TreeNode, ITreeOptions } from 'angular-tree-component';
import { Dictionary } from 'lodash';
import { JsonPipe } from '@angular/common';
import { AngularWaitBarrier } from 'blocking-proxy/built/lib/angular_wait_barrier';


@Component({
	selector: 'reference-component',
	templateUrl: './reference.component.html',
	styleUrls: ['./reference.component.scss']
})

export class ReferenceComponent implements OnInit, AfterViewInit {

	@ViewChild('tree') treeComponent: TreeComponent;
	private _options: ITreeOptions;
	private _docs: Array<any>;
	private _currentDocNode: any;
	public YangName: any;
	public YangKeyword: any;
	public YangPath: any;
	public YangListKey: any;
	public YangDescription: any;
	public YangWriteable: any;
	public YangType: any;
	public YangPresence: any;
	public YangEnums: Array<any>;
	public YangLeafRefPaths: Array<string>;
	public YangStatus: any;
	public YangUnsupportedFeatures: Array<string>;
	public YangConstraint: any;

	constructor() {
		this._options = {
		};
	}
	ngOnInit() {
		this._docs = Array();
		//this.sort(this._docs, model_docs);
		this._docs = model_docs;
	}
	private sort(sortedDocs: Array<any>, unsortedDocs: Array<any>): void {
		if (sortedDocs && unsortedDocs) {
			for (let item of unsortedDocs.sort((a, b) => a.name.localeCompare(b.name))) {
				let copy = Object.create(item);
				copy['children'] = Array();
				sortedDocs.push(copy);
				this.sort(copy['children'], item['children'])
			}
		}
	}
	ngAfterViewInit() {
		this.treeComponent.treeModel.getFirstRoot().setActiveAndVisible().expand();
	}

	public get options(): any {
		return this._options;
	}
	public get nodes(): any {
		return this._docs;
	}
	public onActivate(event: any) {
		this._currentDocNode = event.node;
		this.YangPath = event.node.data._path;
		this.YangName = event.node.data.name;
		this.YangKeyword = event.node.data._keyword;
		this.YangListKey = event.node.data._key;
		this.YangDescription = event.node.data._description;
		this.YangWriteable = event.node.data._writeable;
		this.YangType = event.node.data._type;
		this.YangEnums = event.node.data._enums;
		this.YangPresence = event.node.data._presence;
		this.YangLeafRefPaths = event.node.data._leafref_paths;
		this.YangStatus = event.node.data._status;
		this.YangUnsupportedFeatures = event.node.data._unsupported_feature;
		this.YangConstraint = event.node.data._constraint;
	}
	private makeHyperLink(dataNode: any): string {
		return `<span class="item-link" ng-click="onGotoNode(${dataNode.id})">${dataNode.name}</span>`;
	}
	public onGotoNode(event: any) {
		if (event) {
			let treeNode = this.treeComponent.treeModel.getNodeById(event.id);
			if (treeNode) {
				treeNode.setActiveAndVisible();
			}
		}
	}

	public get isParentAnOperation(): boolean {
		let currentNode = this._currentDocNode;
		while (currentNode && currentNode.parent) {
			switch (currentNode.data._keyword) {
				case 'action':
				case 'rpc':
					return true;
			}
			currentNode = currentNode.parent;
		}
		return false;
	}
	public get restGet(): any {
		if (this.isParentAnOperation) {
			return undefined;
		}
		let restStmt = 'GET http://<ipaddress[:port]>/restconf/data/';
		if (this.YangKeyword === 'list')
			restStmt += this.YangPath + '=<' + this.YangListKey + '>';
		else
			restStmt += this.YangPath;
		switch (this.YangKeyword) {
			case 'module':
			case 'rpc':
			case 'action':
				return undefined;
			case 'list':
			case 'container':
				restStmt += '?depth=1';
				break;
			default:
				break;
		}
		restStmt += '\n';
		restStmt += this.xapiHeader;
		return restStmt;
	}
	public get restPost(): any {
		switch (this.YangKeyword) {
			case 'list':
				let restStmt = 'POST http://<ipaddress[:port]>/restconf/data/'
				restStmt += this.YangPath;
				restStmt += '\n';
				restStmt += this.xapiHeader;
				restStmt += 'Content-Type: application/json\n\n';
				let payload: string = '';
				payload = '';
				for (let child of this._currentDocNode.data.children) {
					switch (child['_keyword']) {
						case 'leaf':
						case 'leaf-list':
							payload += `\t"${child['name']}": <${child['_type']}>,\n`;
							break;
						default:
							break;
					}
				}
				if (payload.length > 0) {
					return restStmt + `{\n${payload.substr(0, payload.length - 2)}\n}`;
				} else {
					return undefined;
				}
			default:
				return undefined;
		}
	}
	public get restPatch(): any {
		switch (this.YangKeyword) {
			case 'list':
			case 'container':
				let restStmt = 'PATCH http://<ipaddress[:port]>/restconf/data/'
				restStmt += this.YangPath;
				if (this.YangKeyword === 'list') {
					restStmt += '=<' + this.YangListKey + '>';
				}
				restStmt += '\n';
				restStmt += this.xapiHeader;
				restStmt += 'Content-Type: application/json\n\n';
				let payload: string = '';
				payload = '';
				for (let child of this._currentDocNode.data.children) {
					switch (child['_keyword']) {
						case 'leaf':
						case 'leaf-list':
							if (child['name'] !== this.YangListKey) {
								payload += `\t"${child['name']}": <${child['_type']}>,\n`;
							}
							break;
						default:
							break;
					}
				}
				if (payload.length > 0) {
					return restStmt + `{\n${payload.substr(0, payload.length - 2)}\n}`;
				} else {
					return undefined;
				}
			default:
				return undefined;
		}
	}
	public get restDelete(): any {
		switch (this.YangKeyword) {
			case 'list':
				let restStmt = `DELETE http://<ipaddress[:port]>/restconf/data/${this.YangPath}=<${this.YangListKey}>`;
				restStmt += '\n';
				restStmt += this.xapiHeader;
				return restStmt;
			default:
				return undefined;
		}
	}
	public get restOperation(): any {
		let restStmt: string = '';
		switch (this.YangKeyword) {
			case 'action':
				restStmt = `POST http://<ipaddress[:port]>/restconf/data/${this.YangPath}`;
				break;
			case 'rpc':
				restStmt = `POST http://<ipaddress[:port]>/restconf/operations/${this.YangPath}`;
				break;
			default:
				return undefined;
		}
		restStmt += '\n';
		restStmt += this.xapiHeader;
		restStmt += 'Content-Type: application/json\n\n';
		let payload: any = {};
		this.buildJson(this.findNode('input'), payload);
		if (Object.keys(payload).length > 0) {
			return restStmt + JSON.stringify(payload, null, 4);
		}
		return restStmt;
	}
	private get xapiHeader(): string {
		if (this._currentDocNode && this._currentDocNode.data._keyword == 'module') {
			return '';
		} else {
			return `X-Api-Key: <api-key returned from authenticate rpc>\n`;
		}
	}
	private findNode(name: string): any {
		return this._currentDocNode.data.children.find((c) => c.name == name);
	}
	private buildJson(dataNode: any, payload: any): any {
		if (dataNode) {
			switch (dataNode._keyword) {
				case 'input':
				case 'container':
					let name = dataNode.name
					if (Object.keys(payload).length === 0) {
						name = `oht:${dataNode.name}`;
					}
					payload[name]= {}
					if (dataNode.children) {
						for (let childNode of dataNode.children) {
							this.buildJson(childNode, payload[name]);
						}
					}
					break;
				default:
					payload[dataNode.name] = `<${dataNode._type}>`;
					break;
			}
		}
		return payload;
	}

	private get ooPath(): string {
		let path: string = '';
		if (this.YangPath) {
			for (let item of this.YangPath.split('/')) {
				if (item === 'oht') {
					path = "OpenHlTest"
				} else {
					path += "." + item[0].toUpperCase() + item.substr(1)
				}
			}
		}
		return path;
	}
	private pythonName(name: string): string {
		if (name) {
			let camelCaseName = '';
			for (let piece of name.split('-')) {
				camelCaseName += piece.substr(0, 1).toUpperCase() + piece.substr(1);
			}
			return camelCaseName;
		} else {
			return '';
		}
	}
	public get pythonClass(): string {
		switch (this.YangKeyword) {
			case 'module':
			case 'list':
			case 'container':
				let classDefinition = `class ${this.pythonName(this.YangName)}(Base):\n\n`;
				classDefinition += this.pythonInit;
				classDefinition += this.pythonClassProperties;
				classDefinition += this.pythonProperties;
				classDefinition += this.pythonMethods;
				classDefinition += this.pythonCreate;
				classDefinition += this.pythonRead;
				classDefinition += this.pythonUpdate;
				classDefinition += this.pythonDelete;
				return classDefinition;
			default:
				return undefined;
		}
	}
	public get pythonInit(): string {
		if (this.YangKeyword === 'module')
			return `\tdef __init__(Transport):\n\n`;
		else
			return `\tdef __init__(self, parent):\n\n`;
	}
	public get pythonClassProperties(): string {
		let classProperties = '';
		for (let childNode of this._currentDocNode.data.children) {
			switch (childNode._keyword) {
				case 'container':
				case 'list':
					classProperties += `\t@property\n`;
					classProperties += `\tdef ${this.pythonName(childNode.name)}(self):\n\n`;
					break;
				default:
					break;
			}
		}
		return classProperties;
	}
	public get pythonProperties(): string {
		let properties = '';
		for (let childNode of this._currentDocNode.data.children) {
			switch (childNode._keyword) {
				case 'leaf':
				case 'leaf-list':
					properties += `\t@property\n`;
					properties += `\tdef ${this.pythonName(childNode.name)}(self):\n\n`;
					break;
				default:
					break;
			}
		}
		return properties;
	}
	public get pythonMethods(): string {
		let methods = '';
		for (let childNode of this._currentDocNode.data.children) {
			switch (childNode._keyword) {
				case 'rpc':
				case 'action':
					methods += `\tdef ${this.pythonName(childNode.name)}(self, input):\n\n`;
					break;
				default:
					break;
			}
		}
		return methods;
	}
	public get pythonUpdate(): string {
		let updateMethod = '';
		switch (this._currentDocNode.data._keyword) {
			case 'container':
			case 'list':
				let parameters = '';
				for (let childNode of this._currentDocNode.data.children) {
					switch (childNode._keyword) {
						case 'leaf':
						case 'leaf-list':
							if (childNode.name !== this._currentDocNode.data._key) {
								parameters += ', ';
								parameters += `${this.pythonName(childNode.name)}=None`;
							}
							break;
						default:
							break;
					}
				}
				if (parameters.length > 0) {
					updateMethod += `\tdef update(self${parameters}):\n\n`;
				}
				break;
			default:
				break;
		}
		return updateMethod;
	}
	public get pythonCreate(): string {
		let createMethod = '';
		switch (this._currentDocNode.data._keyword) {
			case 'list':
				let parameters = '';
				for (let childNode of this._currentDocNode.data.children) {
					switch (childNode._keyword) {
						case 'leaf':
						case 'leaf-list':
							parameters += ', ';
							if (childNode.name === this._currentDocNode.data._key) {
								parameters += `${this.pythonName(childNode.name)}`;
							} else {
								parameters += `${this.pythonName(childNode.name)}=None`;
							}
							break;
						default:
							break;
					}
				}
				createMethod += `\tdef create(self${parameters}):\n\n`;
				break;
			default:
				break;
		}
		return createMethod;
	}
	public get pythonDelete(): string {
		if (this._currentDocNode.data._keyword === 'list') {
			return `\tdef delete(self, ${this.pythonName(this._currentDocNode.data._key)}):\n\n`;
		} else {
			return '';
		}
	}
	public get pythonRead(): string {
		let readMethod = '';
		switch (this._currentDocNode.data._keyword) {
			case 'list':
				let parameters = '';
				for (let childNode of this._currentDocNode.data.children) {
					if (childNode.name === this._currentDocNode.data._key) {
						parameters += ', ';
						parameters += `${this.pythonName(childNode.name)}=None`;
					}
				}
				readMethod += `\tdef read(self${parameters}):\n\n`;
				break;
			default:
				break;
		}
		return readMethod;
	}
}
